* {
  box-sizing: border-box;
}
.container-fluid {
  height: 100vh;
  background-color: hsl(47, 88%, 63%);
}
.card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #121212;
  border-radius: 20px;
  box-shadow: 5px 5px #121212;
  width: 400px !important;
  padding: 24px;
  .img-container {
    border-radius: 14px;
    overflow: hidden;
    img {
      width: 100% !important;
    }
  }
  .card-body {
    padding: 0 !important;
    .cat {
      font-weight: bold;
      padding: 1px 10px;
      margin: 25px 0 10px;
      background-color: #f4d04e;
    }
    .date {
      font-size: 13px;
      padding-bottom: 15px;
    }
    .card-title {
      font-weight: bold;
      width:fit-content;
      cursor: pointer;
      &:hover {
        color: #f4d04e;
      }
    }
    .card-text {
      color: gray;
    }
    .author-info {
      .img-span {
        width: 35px;
        display: inline-block;
        img {
          max-width: 100%;
        }
      }
      .name-span {
        padding-left: 15px;
        font-weight: 600;
      }
    }
  }
}
@media (max-width:405px) {
  .card {
    width: 380px !important;
  }
}
